import { useState, useEffect } from "react";
import axios from "axios";

const useLikePlanner = (userid) => {
  const [likedPlanners, setLikedPlanners] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    // userid  
    if (!userid || userid.trim() === "") {
      setLikedPlanners([]);
      return; // userid   API  
    }

    const fetchLikedPlanners = async () => {
      setLoading(true);
      try {
        //  API 
        const response = await axios.get(`https://www.wanderlog.shop/user/mypage/${userid}/liked-planners`);
        setLikedPlanners(response.data); // API   
      } catch (error) {

      } finally {
        setLoading(false); // 
      }
    };

    fetchLikedPlanners();
  }, [userid]);

  return { likedPlanners, loading, error };
};

export default useLikePlanner;
