import axios from "axios";
import { useState } from "react"
import { useNavigate, useSearchParams } from "react-router-dom";
import "../scss/EmailAuthPage.scss"

const EmailAuthPage = ()=>{
    const [email,setEmail] = useState("");
    const [error,setErorr] = useState("");
    const [message,setMessage] = useState("");
    const navigate = useNavigate();
    const [searchParams] = useSearchParams();
    const userid = searchParams.get("userid"); //URL   ID 

    const handleSubmit = async (e)=>{
        e.preventDefault();
        setErorr("");
        setMessage("");

        try{
            await axios.post("http://localhost:9000/user/send-verify-code",{userid,email});
            setMessage("   .  .");
            navigate(`/verify-code?userid=${userid}&email=${email}`); //   
        }catch(err){
            setErorr("   .  .");
        }
    };


return (
    <div className="email-auth-page">
        <h2 class="email-auth-title"> </h2>
        <form onSubmit={handleSubmit} className="email-auth-form">
            <label htmlFor="email"></label>
            <input 
            type="email"
            id="email"
            value={email}
            onChange={(e)=>setEmail(e.target.value)}
            required
            placeholder=" ."
            />
            <button type="submit" className="email-auth-button">  </button>
        </form>
        {message && <p className="email-auth-message"> {message} </p>}
        {error && <p className="email-auth-error"> {error} </p> }
    </div>
);

};

export default EmailAuthPage;
