import React, { useState, useEffect } from "react";
import axios from "axios";
import useMyPage from "./useMyPage"; // Custom hook import
import useProfileImage from "./useProfileImage";
import "./MyInformation.scss";
import { useLoginStatus } from "../auth/PrivateRoute";
import Header from "../components/Header";
import Edit from "../images/edit.png";
import Setting from "../images/settings.png";
import { previousMonday } from "date-fns";

const MyInformation = ({ detailProfile, setDetailProfile }) => {
  const [isEditing, setIsEditing] = useState(false);
  const [loading, setLoading] = useState(true);
  const [userData, setUserData] = useState(null);
  const [error, setError] = useState(null);
  const { loginData, loginStatus } = useLoginStatus();
  const [isSaveEnabled, setIsSaveEnabled] = useState(false);

  const [originUsername, setOriginUsername] = useState(userData?.username || "");


  //     
  // const [detailProfile, setDetailProfile] = useState(false); //  
  const [isPasswordModalOpen, setIsPasswordModalOpen] = useState(false);

  const handleOpenPasswordModal = () => {
    setIsPasswordModalOpen(true); //
  };

  const handleClosePasswordModal = () => {
    setIsPasswordModalOpen(false); // 
    setValidationMessages({});
    setFormData((prev) => ({ ...prev, nowPassword: "", password: "", repassword: "" }));
  };

  const [isEmailEditing, setIsEmailEditing] = useState(false);
  const [isPasswordValid, setIsPasswordValid] = useState(false); //   

  const [formData, setFormData] = useState({
    profileImage: "/ProfileImg/anonymous.jpg",
    userid: "",
    username: "",
    email: "",
    // gender: "",
  });
  const {
    setValidationMessages,
    validationMessages,
    authCodeSent,
    timer,
    isAuthCodeLocked,
    validatePassword,
    handlePasswordEditClick,
    handleCancelPasswordEditing,
    validateEmail,
    isPasswordEditing,
    setIsPasswordEditing,
    handleChange,
    sendAuthCode,
    verifyAuthCode,
    resetAuthState,
  } = useMyPage(formData, setFormData);

  const {
    imagePreview,
    fileInputRef,
    handleFileInputClick,
    handleFileChange,
    handleDrop,
    handleDragOver,
    handleCancelImage,
    handleResetToDefaultImage,
    setImagePreview,
  } = useProfileImage(formData, setFormData, setIsSaveEnabled, userData); //   

  useEffect(() => {
    if (userData?.username) {
      setOriginUsername(userData.username);
    }
  }, [userData?.username]);

  // Fetch user data on load
  useEffect(() => {
    const fetchUserData = async () => {
      try {
        setLoading(true);

        //   
        const userResponse = await axios.get(
          "https://www.wanderlog.shop/user/mypage",
          {
            withCredentials: true,
          }
        );

        setUserData(userResponse.data);
        setFormData({
          profileImage: userResponse.data.img || "/ProfileImg/anonymous.jpg",
          userid: userResponse.data.userid,
          username: userResponse.data.username,
          email: userResponse.data.email,
          birth: userResponse.data.birth,
          password: "",
          nowPassword: userResponse.data.password,
          // gender: userResponse.data.gender,
        });
        setLoading(false);

      } catch (err) {
        console.error(":", err);
        setError("     .");
        setLoading(false);
      }
    };

    fetchUserData();
  }, []); // userData  //   `userData`    

  if (loading) return <div> ...</div>;
  if (error) return <div>{error}</div>;

  //         
  const handleEditClick = () => {
    if (userData) {
      setImagePreview(
        userData.img
          ? `http://localhost:9000${userData.img}`
          : "/ProfileImg/anonymous.jpg"
      );
      setFormData({
        profileImage: userData.img || "/ProfileImg/anonymous.jpg",
        userid: userData.userid,
        username: userData.username,
        email: userData.email,
      });
    }
    setIsEditing(true); //   
  };

  const handleCancelChanges = () => {
    setFormData({
      profileImage: userData.img || "/ProfileImg/anonymous.jpg",
      userid: userData.userid,
      username: userData.username,
      email: userData.email,
      password: "",
      repassword: "",
    });
    setIsEditing(false);
  };

  const handleCancelEmailEditing = () => {
    setIsEmailEditing(false);
    setFormData((prev) => ({
      ...prev,
      email: userData.email,
    }));
    resetAuthState();
  };

  const handleEmailChange = (e) => {
    const { value } = e.target;
    setFormData((prev) => ({
      ...prev,
      email: value,
    }));

    //     
    resetAuthState();
    setValidationMessages((prev) => ({
      ...prev,
      email: "",
      authCode: "",
    }));
  };

  const handlePasswordChange = async (e) => {
    const { name, value } = e.target;

    //  
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));

    //    
    if (name === "nowPassword") {
      try {
        const response = await axios.post(
          "https://www.wanderlog.shop/user/mypage/check-password",
          { password: value },
          { withCredentials: true }
        );

        setValidationMessages((prev) => ({
          ...prev,
          nowPassword: "  ",
          nowPasswordColor: "validation-success",
        }));
      } catch (error) {
        setValidationMessages((prev) => ({
          ...prev,
          nowPassword: "   .",
          nowPasswordColor: "validation-error",
        }));
      }
    }

    //   
    if (name === "password") {
      const validationResult = validatePassword(value);
      setValidationMessages((prev) => ({
        ...prev,
        password: validationResult.message,
        passwordColor: validationResult.color,
      }));
    }

    if (name === "repassword") {
      if (formData.password !== value) {
        setValidationMessages((prev) => ({
          ...prev,
          repassword: "   ",
          repasswordColor: "validation-error",
        }));
        setIsPasswordValid(false);
      } else {
        setValidationMessages((prev) => ({
          ...prev,
          repassword: " .",
          repasswordColor: "validation-success",
        }));
        setIsPasswordValid(true);
      }
    }
  };


  const handleSaveChanges = async () => {

    try {
      if (formData.password || formData.reapssword) {
        const passwordCheckResponse = await axios.post(
          "https://www.wanderlog.shop/user/mypage/check-password",
          { password: formData.nowPassword },
          { withCredentials: true }
        );

        if (passwordCheckResponse.data !== " ") {
          alert("   .");
          return;
        }

        if (!formData.password || !formData.repassword) {
          alert("  !");
          return;
        }
        if (formData.password !== formData.repassword) {
          alert("   .");
          return;
        }

        // ,  ,  
        const passwordValidationResult = validatePassword(formData.password);
        if (passwordValidationResult.color === "validation-error") {
          alert(passwordValidationResult.message);
          return;
        }
      }

      const updateData = {};
      if (formData.username) updateData.username = formData.username;
      if (formData.profileImage) updateData.profileImage = formData.profileImage;
      if (formData.password && formData.repassword) {
        updateData.password = formData.password;
        updateData.repassword = formData.repassword;
      }

      //   
      await axios.put(
        "https://www.wanderlog.shop/user/mypage/userupdate", updateData,
        { withCredentials: true }
      );
      alert("   .");
      window.location.reload();
      setFormData((prev) => ({ ...prev, nowPassword: "", password: "", repassword: "" }));
      setIsPasswordEditing(false);
      setValidationMessages({});
      setIsPasswordModalOpen(false);
      setDetailProfile(false);
    } catch (err) {
      console.error("    :", err);
      alert("     .");

    }
  };

  //     (,  )
  const validateUsername = (username) => {
    const usernameRegex = /^[a-zA-Z-]{2,15}$/u; // ,  
    if (!usernameRegex.test(username)) {
      setValidationMessages((prev) => ({
        ...prev,
        username: "       , 2~20 .",
        usernameColor: "validation-error",
      }));
      return false;
    } else {
      setValidationMessages((prev) => ({
        ...prev,
        username: "",
        usernameColor: "validation-success",
      }));
      return true;
    }
  };

  const handleUsernameChange = (e) => {
    const { value } = e.target;
    setFormData((prev) => ({ ...prev, username: value }));

    //    
    const isValid = validateUsername(value);

    if (isValid && value !== originUsername) {
      setIsSaveEnabled(true);
    } else {
      setIsSaveEnabled(false);
    }
  };

  const handleEmailValidation = (e) => {
    const { value } = e.target;
    setFormData((prev) => ({ ...prev, email: value }));

    //   
    validateEmail(value);
  };

  const handleDelet = async () => {
    if (!window.confirm("  ?")) {
      return;
    }

    try {
      const response = await axios.delete(
        "https://www.wanderlog.shop/user/mypage/delete",
        {
          withCredentials: true, //     
        }
      );

      if (response.status === 200) {
        alert(" .");
        //    
        setUserData(null); //   
        window.location.href = "/"; //   
      }
    } catch (error) {
      console.error("   :", error);
      alert("   .  .");
    }
  };

  const handleProfileButton = () => {
    if (detailProfile) {
    }
  };

  return (

    <div className="mypage-container">
      {!detailProfile ? (
        <div className="mypage-profile">
          <div className="mypage-profile__image">
            <img
              src={
                imagePreview ||
                (userData.img
                  ? `http://localhost:9000${userData.img}`
                  : "/ProfileImg/anonymous.jpg")
              }
              alt=" "
              className="mypage-profile__img"
            />
            <button
              onClick={() => {
                setDetailProfile(true);
              }}
              className="detailProfile-button"
              title=" "
            >
              <img src={Setting} alt="" className="setting-image" content="" />
            </button>

          </div>
          <h2>{userData.username} !</h2>
        </div>
      ) : (
        //  
        <div className="detail-profile">
          <div className="profile-header">

            <img
              src={
                imagePreview ||
                (userData.img
                  ? `http://localhost:9000${userData.img}`
                  : "/ProfileImg/anonymous.jpg")
              }
              alt=" "
              className="profile-img"
            />
            <button onClick={() => fileInputRef.current?.click()} className="editButton">
              <img src={Edit} alt="" />
            </button>
            <input
              type="file"
              ref={fileInputRef}
              accept="image/*"
              style={{ display: "none" }}
              onChange={handleFileChange}
            />
            <h2 className="detail-username">{userData.username}</h2>
          </div>

          <div className="detail-userInfo">
            <h2> </h2>
            <div className="detail-input mb-3">
              <label className="d-block mb-2 detail-input_label"></label>
              <input
                type="text"
                className="form-control mb-3"
                placeholder={userData.email}
                aria-label=""
                disabled
              />

              <label className="d-block mb-2 detail-input_label"></label>
              <input
                type="text"
                className="form-control mb-3"
                aria-label=""
                aria-describedby="basic-addon2"
                value={formData.username}
                onChange={handleUsernameChange}
                defaultValue={userData.username}
              />
              {validationMessages.username && (
                <div className={`validation-message ${validationMessages.usernameColor}`}>
                  {validationMessages.username}
                </div>
              )}

              <label className="d-block mb-2 detail-input_label">
                
              </label>
              <input
                type="text"
                className="form-control mb-3"
                aria-label=""
                aria-describedby="basic-addon2"
                defaultValue={userData.birth}
                disabled
              />

              <div className="detail-password">
                <label className="d-block mb-2 detail-input_label">
                  
                </label>
                <input
                  type="password"
                  className="form-control mb-3"
                  aria-label=""
                  aria-describedby="basic-addon2"
                  value="********"
                  disabled
                />
                <button
                  className="btn btn-secondary btn-sm pweditButton"
                  onClick={handleOpenPasswordModal}
                >
                   
                </button>

                {isPasswordModalOpen && (
                  <div className="password-modal">
                    <div className="modal-content">
                      <h2> </h2>
                      {/* Password Input */}
                      <input
                        type="password"
                        name="nowPassword"
                        placeholder=" "
                        className="form-control mb-2"
                        onChange={handlePasswordChange}
                      />
                      {validationMessages.nowPassword && (
                        <div
                          className={`validation-message ${validationMessages.nowPasswordColor}`}
                        >
                          {validationMessages.nowPassword}
                        </div>
                      )}
                      <input
                        type="password"
                        name="password"
                        placeholder=" "
                        className="form-control mb-2"
                        onChange={handlePasswordChange}
                      />
                      {validationMessages.password && (
                        <div
                          className={`validation-message ${validationMessages.passwordColor}`}
                        >
                          {validationMessages.password}
                        </div>
                      )}
                      <input
                        type="password"
                        name="repassword"
                        placeholder="  "
                        className="form-control"
                        onChange={handlePasswordChange}
                      />
                      {validationMessages.repassword && (
                        <div
                          className={`validation-message ${validationMessages.repasswordColor}`}
                        >
                          {validationMessages.repassword}
                        </div>
                      )}

                      <div className="modal-buttons">
                        <button
                          className="btn btn-secondary"
                          onClick={handleClosePasswordModal}
                        >
                          
                        </button>
                        <button
                          className="btn btn-primary"
                          onClick={handleSaveChanges}
                        >
                          
                        </button>
                      </div>
                    </div>
                  </div>
                )}
              </div>
            </div>

            <div className="detail-buttons">
              <div className="backAndSaveBtn">
                <button
                  onClick={() => {
                    setDetailProfile(false);
                  }}
                  className="btn btn-secondary"
                >
                   
                </button>
                <button
                  onClick={handleSaveChanges}
                  className="btn btn-primary"
                  disabled={!isSaveEnabled} //   
                >
                  
                </button>
              </div>
              <div className="detail-userDelete my-3">
                <button
                  // onClick={}
                  className="delete-btn btn btn-danger"
                >
                  
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default MyInformation;
