

import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { useEffect, useState, useRef } from "react";


// Swiper  import 
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/effect-coverflow';
import 'swiper/css/autoplay';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import { Autoplay } from 'swiper/modules';
import './BoardSection.scss';

const BoardSection = () => {
    const [planners, setPlanners] = useState([]);
    const pageSize = 8; //     

    const navigate = useNavigate();
    const swiperRef = useRef(null);

    const handlePlannerClick = (plannerID) => {
        const plannerItem = planners.find((item) => item.plannerID === plannerID);
        navigate(`planner/board/destination?plannerID=${plannerID}`, { state: { plannerItem } });
    };

    const fetchPlanners = (page) => {
        axios.get(`https://www.wanderlog.shop/planner/list?page=${-1}&size=${pageSize}`)
            .then((response) => {
                setPlanners(response.data.content); //  
            })
            .catch((error) => {
                console.error("  ", error);
            });
    };

    useEffect(() => {
        fetchPlanners();
        if (swiperRef.current) {
            // Autoplay   (      )
            swiperRef.current.autoplay?.start();
        }
    }, [pageSize])

    return (
        <section className="swiper-section">
            <div className="board-section">
                <div className="board-top">
                    <strong>   </strong>
                    <a href="/planner/board" className="material-symbols-outlined">
                        add
                    </a>
                </div>


                <Swiper
                    onSwiper={(swiper) => {
                        swiperRef.current = swiper; // Swiper  ref 
                        swiper.autoplay?.start();  //   autoplay 
                    }}
                    onSlideChange={() => {
                        swiperRef.current?.autoplay?.start(); //    autoplay 
                    }}
                    slidesPerView={4}
                    spaceBetween={20}
                    speed={1500}
                    loop={true}
                    autoplay={{
                        delay: 2000,
                        disableOnInteraction: false, //        
                    }}

                    pagination={{
                        clickable: true, //     
                    }}
                    modules={[Autoplay]}
                    className="mySwiper"


                >
                    {planners.map((planner) => (
                        <SwiperSlide
                            key={planner.plannerID}
                            onClick={() => handlePlannerClick(planner.plannerID)}
                            onMouseEnter={() => {
                                swiperRef.current?.autoplay?.stop(); //      autoplay 
                            }}
                            onMouseLeave={() => {
                                swiperRef.current?.autoplay?.start(); //     autoplay  
                            }}
                        >
                            <div className="main-planner-item">
                                <div className="main-planner-thumbnail">
                                    <img src={planner.thumbnailImage} alt=" " />
                                </div>
                                <div className="main-planner-info">
                                    <h3 className="main-planner-title">{planner.plannerTitle}</h3>
                                    <p className="main-planner-username"> : {planner.username}</p>
                                    <p className="main-planner-duration">
                                        {planner.day === 1
                                            ? ''
                                            : `${planner.day - 1} ${planner.day}`}
                                    </p>
                                    <p className="main-planner-created-at">
                                         {new Date(planner.createAt).toLocaleDateString()}
                                    </p>
                                </div>
                            </div>
                        </SwiperSlide>
                    ))}
                </Swiper>
            </div>
        </section>
    );
};

export default BoardSection;
