import { useState, useEffect, useRef, useCallback } from "react";
import axios from "axios";

const useMyPage = () => {
  const [formData, setFormData] = useState({
    userid: "",
    username: "",
    password: "",
    repassword: "",
    email: "",
    profileImage: "/ProfileImg/anonymous.jpg",
    authCode: "",
  });

  const [imagePreview, setImagePreview] = useState(formData.profileImage);
  const [validationMessages, setValidationMessages] = useState({});
  const [authCodeSent, setAuthCodeSent] = useState(false);
  const [timer, setTimer] = useState(180);
  const [isAuthCodeVerified, setIsAuthCodeVerified] = useState(false);
  const [isAuthCodeLocked, setIsAuthCodeLocked] = useState(false);
  const [isPasswordValidationVisible, setIsPasswordValidationVisible] = useState(false);

  
  //     ()
  const [isPasswordEditing, setIsPasswordEditing] = useState(false);  //     
  const fileInputRef = useRef(null);

  const handlePasswordEditClick = () => {
    setIsPasswordEditing(true); //    
    setIsPasswordValidationVisible(true); //    
  };
  
  const handleCancelPasswordEditing = () => {
    setIsPasswordEditing(false); //   
    setIsPasswordValidationVisible(false); //   
    setFormData((prev) => ({
      ...prev,
      password: "", //  
      repassword: "", //   
    }));
    setValidationMessages((prev) => ({
      ...prev,
      password: "", //   
      repassword: "", //   
    }));
  };

  
     //   
     const validatePassword = (password) => {
      const passwordRegex = /^(?=.*[a-zA-Z])(?=.*\d)[a-zA-Z\d@$!%*?&]{8,15}$/;
      if (!password) {
        return { message: " .", color: "validation-error" };
      }
  
      if (/\s/.test(password)) {
        return { message: "    .", color: "validation-error" };
      }
  
      if (!passwordRegex.test(password)) {
        return { message: " + , 8~15 .", color: "validation-error" };
      }
  
      return { message: " .", color: "validation-success" };
    };
  
  //  

  //  
  useEffect(() => {
    if (authCodeSent && timer > 0 && !isAuthCodeVerified) {
      const interval = setInterval(() => setTimer((prev) => prev - 1), 1000);
      return () => clearInterval(interval);
    }
  }, [authCodeSent, timer, isAuthCodeVerified]);

  //   
  const resetAuthState = () => {
    setAuthCodeSent(false);
    setIsAuthCodeVerified(false);
    setIsAuthCodeLocked(false);
  };


  const validateEmail = async (email) => {
    //    
    if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email)) {
      setValidationMessages((prev) => ({
        ...prev,
        email: "   .",
        emailColor: "validation-error",
      }));
      return false;
    }
  
    //    
    setValidationMessages((prev) => ({
      ...prev,
      email: " .",
      emailColor: "validation-success",
    }));
  
    try {
      //     
      await sendAuthCode();
    } catch (error) {
      console.error("    :", error.response?.data || error.message);
      setValidationMessages((prev) => ({
        ...prev,
        email: "     .",
        emailColor: "validation-error",
      }));
    }
  };
  
  //    
  const sendAuthCode = async () => {
    if (!formData.email) {
      setValidationMessages((prev) => ({
        ...prev,
        email: " .",
        emailColor: "validation-error",
      }));
      return;
    }
  
    try {
      const response = await axios.post(
        "https://www.wanderlog.shop/user/send-auth-code",
        { email: formData.email }, //  
        { headers: { "Content-Type": "application/json" } }
      );
  
      if (response.data.status === "success") {
        setAuthCodeSent(true); //    
        setValidationMessages((prev) => ({
          ...prev,
          email: "  .",
          emailColor: "validation-success",
        }));
      } else {
        setValidationMessages((prev) => ({
          ...prev,
          email: response.data.message || "   .",
          emailColor: "validation-error",
        }));
      }
    } catch (error) {
      console.error("   :", error.response?.data || error.message);
      setValidationMessages((prev) => ({
        ...prev,
        email: "      .",
        emailColor: "validation-error",
      }));
    }
  };

  const verifyAuthCode = async () => {
    try {
        const response = await axios.post("https://www.wanderlog.shop/user/verify-auth-code", {
            email: formData.email,
            code: formData.authCode,
        });

        if (response.data.message.includes("")) {
            setIsAuthCodeVerified(true);
            setIsAuthCodeLocked(true); //     
            setValidationMessages((prevMessages) => ({
                ...prevMessages,
                authCode: " .",
                authCodeColor: "validation-success", //  
            }));
        } else {
            setValidationMessages((prevMessages) => ({
                ...prevMessages,
                authCode: "  .",
                authCodeColor: "validation-error", //  
            }));
        }
    } catch (error) {
        setValidationMessages((prevMessages) => ({
            ...prevMessages,
            authCode: "     .",
            authCodeColor: "validation-error", //  
        }));
    }
};

  
  return {
    formData,
    handlePasswordEditClick,
    handleCancelPasswordEditing,
    setFormData,
    imagePreview,
    setImagePreview,
    validationMessages,
    setValidationMessages,
    authCodeSent,
    setAuthCodeSent,
    timer,
    isPasswordEditing,
    handlePasswordEditClick,
    handleCancelPasswordEditing,
    setTimer,
    isPasswordEditing,  //       
    setIsPasswordEditing,  //      
    setValidationMessages,
    isAuthCodeVerified,
    setIsAuthCodeVerified,
    isAuthCodeLocked,
    setIsAuthCodeLocked,
    setIsPasswordEditing,
    fileInputRef,
    // handleDrop: (e) => {
    //   e.preventDefault();
    //   if (e.dataTransfer.files.length > 0) {
    //     handleImageUpload(e.dataTransfer.files[0]); //      
    //   }
    // },
    handleDragOver: (e) => e.preventDefault(),
    handleFileInputClick: () => fileInputRef.current?.click(),
    // handleImageUpload,
    // handleCancelImage: () => {
    //   setImagePreview("/ProfileImg/anonymous.jpg");
    //   setFormData((prev) => ({
    //     ...prev,
    //     profileImage: "/ProfileImg/anonymous.jpg", //   
    //   }));
    // },
    handleChange: (e) => {
      const { name, value } = e.target;
      setFormData((prev) => ({ ...prev, [name]: value }));
    },
    sendAuthCode,
    validateEmail,
    verifyAuthCode,
    setImagePreview,
    resetAuthState,
    validatePassword,
    validatePassword,
    handlePasswordEditClick,
    // handleFileChange: (e) => handleImageUpload(e.target.files[0]),
    setIsPasswordValidationVisible,
  };
};

export default useMyPage;



