import { useState } from "react";
import { useSearchParams } from "react-router-dom";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import "../scss/PasswordResetPage.scss"

const PasswordResetPage = ()=>{
    const [newPassword,setNewPassword] = useState("");
    const [confirmPassword,setConfirmPassword] = useState("");
    const [message, setMessage] = useState("");
    const [error,setError] = useState("");
    const navigate = useNavigate();
    const [searchParams] = useSearchParams();

    const userid = searchParams.get("userid")
    const handleHome = () => window.location.href = "/" ;

    const handleSubmit = async (e) =>{
        e.preventDefault();
        if(newPassword !== confirmPassword){
            setError("  ");
            return ;
        }
        setMessage("");
        setError("");
        try{
            const token = localStorage.getItem("resetToken")?.trim();
            await axios.post("http://localhost:9000/user/reset-password",
            { newPassword }, {
                headers : {Authorization : `Bearer ${token}`},
            });
            setMessage("  ");
            alert("  ");
            setTimeout(()=>{
                window.location.href = "/";
            },2000); //  2  
            localStorage.removeItem("resetToken"); //     
        }catch(err){
            setError("  .     ");
            localStorage.removeItem("resetToken"); //     
            alert("   ");
            navigate(`/email-auth?userid=${userid}`);
        }
    };

    return (
        <div className="password-reset-page">
            <div className="reset-header" onClick={handleHome}>WanderLog</div>
            <h2 className="password-reset-title"> </h2>
                <form onSubmit={handleSubmit} className="password-reset-form">
                    <label htmlFor="newPassword"> </label>
                    <input 
                    type="password"
                    id="newPassword"
                    value={newPassword}
                    onChange={(e)=>setNewPassword(e.target.value)}
                    placeholder="  "
                    required
                    />
                    <label htmlFor="confirmPassword"> </label>
                    <input 
                    type="password"
                    id="confirmPassword"
                    value={confirmPassword}
                    onChange={(e)=>setConfirmPassword(e.target.value)}
                    placeholder="  "
                    required />
                    <button type="submit" className="password-reset-button"> </button>
                </form>
                {message && <p className="password-reset-message">{message}</p>}
                {error && <p className="password-reset-error">{error}</p>}
        </div>
    )

}

export default PasswordResetPage;