import { useEffect, useState } from "react";
import axios from "axios";
import { useNavigate } from 'react-router-dom';
import './Board.scss';
import Footer from '../components/Footer.jsx';
import { useLoginStatus } from "../auth/PrivateRoute.jsx";
import MainSection from "../main/MainSection.jsx";
import Sidebar from "../components/Sidebar.jsx";

const Board = () => {
    const [planners, setPlanners] = useState([]);
    const [currentPage, setCurrentPage] = useState(1); //  
    const [totalPages, setTotalPages] = useState(1); //   
    const [totalItems, setTotalItems] = useState(0); //    ()
    const [selectedArea, setSelectedArea] = useState(""); //   
    const [showMyCoursesOnly, setShowMyCoursesOnly] = useState(false); //    
    const [activeTab, setActiveTab] = useState("board-userCourse"); //    
    const [loading, setLoading] = useState(false); //   
    const [error, setError] = useState(null); //  
    const [noDataMessage, setNoDataMessage] = useState(""); //     

    const pageSize = 16; //     
    const navigate = useNavigate();
    const { loginStatus, loginData } = useLoginStatus();

    const handlePlannerClick = (plannerID) => {
        const plannerItem = planners.find((item) => item.plannerID === plannerID);
        navigate(`destination?plannerID=${plannerID}`, { state: { plannerItem } });
    };

    const fetchPlanners = async (page) => {
        setLoading(true);
        setError(null);
        setNoDataMessage("");

        const query = {
            page: page,
            size: pageSize,
            ...(selectedArea && { area: selectedArea }), //  
            ...(showMyCoursesOnly && { myCourses: true, userId: loginData?.userid }), //    
            ...(!showMyCoursesOnly && { isPublic: 1 }), //   
        };

        try {
            const response = await axios.get(`https://www.wanderlog.shop/planner/board`, { params: query });
            const fetchedPlanners = response.data.content;

            //      
            if (fetchedPlanners.length === 0) {
                setNoDataMessage("    .");
            } else {
                setPlanners((prev) => (page === 1 ? fetchedPlanners : [...prev, ...fetchedPlanners]));
                setTotalPages(response.data.totalPages); //    
                setTotalItems(response.data.totalElements); //     ()
            }
        } catch (err) {
            setError("   .");
        } finally {
            setLoading(false);
        }
    };
    

    const handleMoreClick = () => {
        if (currentPage < totalPages) {
            const nextPage = currentPage + 1;
            setCurrentPage(nextPage);
            fetchPlanners(nextPage);
        }
    };

    const handleTabClick = (tab) => {
        setActiveTab(tab); //   
        setPlanners([]); //     
        setCurrentPage(1); //  

        if (tab === "board-myCourse") {
            setShowMyCoursesOnly(true); //    
        } else {
            setShowMyCoursesOnly(false); //   
        }
    };

    useEffect(() => {
        if (activeTab === "board-myCourse" && !loginStatus) {
            setPlanners([]); //     
            setTotalPages(1); //   
            setTotalItems(0); //    
            return;
        }
        fetchPlanners(1);
    }, [selectedArea, showMyCoursesOnly, activeTab, loginStatus]);

    return (
        <>
            <div className="board">
                <MainSection />
                <Sidebar />
                <div className="board-wrapper">

                    {/*   */}
                    <div className="board-tap">
                        <ul>
                            <li
                                className={`board-myCourse ${activeTab === "board-myCourse" ? "on" : ""}`}
                                onClick={() => handleTabClick("board-myCourse")}
                                style={activeTab === "board-myCourse" ? { pointerEvents: "none"} : {}}>
                                <span>  </span>
                            </li>
                            <li
                                className={`board-userCourse ${activeTab === "board-userCourse" ? "on" : ""}`}
                                onClick={() => handleTabClick("board-userCourse")}
                                style={activeTab === "board-userCourse" ? { pointerEvents: "none"} : {}}>
                                <span>  </span>
                            </li>
                        </ul>
                    </div>

                    <div className="board-top">
                        <h1 className="board-title">
                            {activeTab === "board-myCourse" ? "     !" : "     !"}
                        </h1>
                        
                        {/*   */}
                        {activeTab === "board-userCourse" && (
                            <div className="selectBox">
                                <select
                                    className="select"
                                    value={selectedArea}
                                    onChange={(e) => setSelectedArea(e.target.value)}
                                >
                                    <option value=""></option>
                                    <option value=""></option> {/*      . DB    */}
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                </select>
                            </div>
                        )}

                    </div>

                    {/*   */}
                    <div className="planner-content">
                        {loading ? (
                            <p className="loading"> ...</p>
                        ) : error ? (
                            <p className="error">{error}</p>
                        ) : noDataMessage ? (
                            <p className="noData">{noDataMessage}</p> //    
                        ) : planners.length === 0 ? (
                            <p className="noCourse">
                                {activeTab === "board-myCourse" && !loginStatus && "   ."}
                            </p>
                        ) : (
                            planners.map((planner) => (
                                <div
                                    key={planner.plannerID}
                                    className="planner-item"
                                    onClick={() => handlePlannerClick(planner.plannerID)} >
                                    <div className="planner-image-section">
                                        <div className="planner-thumbnail">
                                            <img src={planner.thumbnailImage} alt=" " />
                                        </div>
                                        <div className="planner-profile">
                                            <img src={planner.userProfileImg}></img>
                                        </div>
                                    </div>
                                    <div className="planner-info">
                                        <p className="planner-duration">
                                            {planner.day === 1
                                                ? ""
                                                : `${planner.day - 1} ${planner.day}`}
                                        </p>
                                        <h3 className="planner-title">{planner.plannerTitle}</h3>
                                        <p className="planner-area">{planner.area}</p>
                                        <p className="planner-created-at"> {new Date(planner.createAt).toLocaleDateString()}</p>
                                        <div className="planner-like-count">
                                            <span className="material-symbols-outlined like-icon">
                                                favorite
                                            </span>
                                            <span className="like-number">{planner.likeCount}</span>
                                        </div>
                                    </div>
                                </div>
                            ))
                        )}
                    </div>

                    {/*   */}
                    {!loading && !error && !noDataMessage && planners.length > 0 && currentPage < totalPages && (
                        <div className="board-pagination">
                            <button onClick={handleMoreClick}> +</button>
                            <p className="pagination-info">
                                {planners.length} / {totalItems} 
                            </p>
                        </div>
                    )}
                </div>
            </div>
            <Footer />
        </>
    );
};

export default Board;
