import React, { createContext, useState, useContext, useEffect } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

// Context 
const LoginContext = createContext();

//     Hook
export const useLoginStatus = () => useContext(LoginContext);

export const LoginProvider = ({ children }) => {

    const [loginStatus, setLoginStatus] = useState(false);  //  
    const [loginData, setLoginData] = useState(null);  //  
    const navigate = useNavigate();  // navigate  

    //   
    useEffect(() => {
        const validateCookie = async () => {
            try {
                const response = await axios.post(
                    "https://www.wanderlog.shop/api/cookie/validate",
                    {},
                    { withCredentials: true }
                );

                //      
                setLoginStatus(true);
                setLoginData(response.data);
            } catch (err) {
                await handleTokenRefresh();
            }
        };

        const handleTokenRefresh = async () => {
            const userid = localStorage.getItem("userid");
            if (!userid) {
                console.error("userid   ");
                setLoginStatus(false);
                setLoginData(null);
                return;
            }

            try {
                await refreshAccessToken(userid);  //   
                const retryResponse = await axios.post(
                    "http://localhost:9000/api/cookie/validate",
                    {},
                    { withCredentials: true }
                );
                //      
                setLoginStatus(true);
                setLoginData(retryResponse.data);
            } catch (refreshError) {
                console.error("  :", refreshError);
                setLoginStatus(false);
                setLoginData(null);
                localStorage.removeItem("userid");
            }
        };

        validateCookie();  //   

    }, [navigate]);

    return (
        <LoginContext.Provider value={{ loginStatus, loginData }}>
            {children}
        </LoginContext.Provider>
    );
};

//    
const refreshAccessToken = async (userid) => {
    try {
        const response = await axios.post(
            'http://localhost:9000/api/cookie/refresh',
            { userid },
            { withCredentials: true, headers: { userid } }
        );
        return response.data;
    } catch (error) {
        localStorage.removeItem("userid");
        throw new Error("   ");
    }
};

// PrivateRoute 
const PrivateRoute = ({ element, ...rest }) => {
    const { loginStatus } = useLoginStatus();  //   
    const navigate = useNavigate();  // navigate  

    //   
    if (loginStatus === null) {
        return <div>Loading...</div>;  //      
    }

    if (!loginStatus) {
        navigate("/user/login");  //     
        return null;  //   
    }

    //     
    return React.cloneElement(element, { loginStatus });
};

export default PrivateRoute;
